import { useState, useEffect } from "react";
import { io } from "socket.io-client"
import styled from "styled-components";

const Wrapper = styled.div`
  font-size: 4rem;
  display: flex;
  margin-top: auto;
`;

const Chatbox = () => {
  const loggedInUser = localStorage.getItem("user");
  const [useropen, setUserOpen] = useState(false);
  const [open, setOpen] = useState(false);
  const [users, setUsers] = useState();
  const [messages, setMessages] = useState([]);
  const [messageinfo, setMessageinfo] = useState({ user: "", msg: "" });
  const { user, msg } = messageinfo;

  useEffect(() => {
    fetch("/api/display_follows")
      .then((response) => response.json())
      .then((data) => setUsers(data));
  }, []);

  function userChat(e) {
    const socket = io.connect("http://localhost:8080")
    socket.on("connect", () => {
        console.log("connected")
    })
    setUserOpen(true);
    const name = e.target.getAttribute("name");
    setMessageinfo((prevInfo) => {
      return { ...prevInfo, user: name };
    });
  }

  function sendMessageinfo() {

  }

  function handleInput(e) {
    setMessageinfo((prevInfo) => {
      return { ...prevInfo, msg: e.target.value };
    });
  }

  function openChange() {
    setOpen((prevInfo) => !prevInfo);
  }

  function openChangeUser() {
    setMessages();
    setUserOpen(false);
  }

  return (
    <Wrapper>
      {open ? (
        useropen ? (
          <div>
            <button onClick={openChangeUser}>Back</button>
            <br />
            {loggedInUser} you are in a chat with {user}!
            {messages &&
              messages.map((message) => {
                return (
                  <div key={message._id}>
                    {loggedInUser === message.sender ? (
                      <div>{message.msg}</div>
                    ) : (
                      <div>{message.msg}</div>
                    )}
                  </div>
                );
              })}
            <input type="text" name="msg" value={msg} onChange={handleInput} />
            <br />
            <button onClick={sendMessageinfo}>Submit</button>
          </div>
        ) : (
          <div>
            {users &&
              users.map((user) => {
                return (
                  <div
                    key={user.id}
                    className="user-link-chat"
                    onClick={userChat}
                    name={user.username}
                    >
                    {user.username}
                  </div>
                );
              })}
              <button onClick={openChange}>Close Chat</button>
          </div>
        )
      ) : (
        <button onClick={openChange}>Open Chat</button>
      )}
    </Wrapper>
  );
};

export default Chatbox;
