import { useState, useEffect } from "react"

const Followrequests = () => {

    const [ follow_requests, setRequests ] = useState()
    
    useEffect(() => {
        fetch("/api/display_follow_requests")
        .then(res => res.json())
        .then(res => setRequests(res))
        .catch(error => console.log(error))
    }, [])
    
    function accept_follow(e) {
        const user_id = e.target.name
        fetch("/api/accept_follow", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ user_id })    
        })
    }
    
    function decline_follow(e) {
        const user_id = e.target.name
        fetch("/api/decline_follow", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ user_id })    
        })
    }
    
    return (
        <>
        {follow_requests && Object.entries(follow_requests).length ? follow_requests.map(request => {
            return (
                <div key={request.id}>
                <p>Request</p>
                <button name={request.id} onClick={accept_follow}>
                    Accept
                </button>
                <button name={request.id} onClick={decline_follow}>
                    Decline
                </button>
                </div>
            )
        })
        :
        <div>No follow requests</div>
        }
        </>
    )
}

export default Followrequests;
