import styled from "styled-components";

const Wrapper = styled.div` 
    background: #525255;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    z-index: 1;

    @media (max-width: 768px) {
        flex-direction: column;
    }
`;

const Div = styled.div`
    list-style: none;
    font-size: 2rem;
    margin: 2rem;
    margin-top: 2rem;
`;

const Footer = () => {
    return (
        <Wrapper>
            <Div>Copyright@2021</Div>
            <Div>About</Div>
            <Div>Contact</Div>
        </Wrapper>
    )
}

export default Footer;
